import {
  ChangeDetectionStrategy,
  Component,
  ElementRef,
  EventEmitter,
  Input,
  OnChanges,
  Output,
  SimpleChanges,
  ViewChild,
} from '@angular/core';
import { MapHelpersService } from '../../components/bb-map-ui/map-helpers.service';
import { MapComponent, Marker } from '../../components/bb-map-ui/map.component';
import { MarkerIcons, Place, PlaceItem } from '../../model/place-model';
import { PlacesGetParams } from '../../model/place-param-model';

declare var $localize: any;
@Component({
  selector: 'bb-map-wrapper',
  templateUrl: './map-wrapper.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class MapWrapperComponent implements OnChanges {
  /**
   * API key need for the google maps to work.
   */
  @Input() apiKey = '';
  /**
   * Map config.
   */
  @Input() config = {};
  /**
   * Map zoom.
   */
  @Input() mapZoom = 0;
  /**
   * Latitude for the coordinates.
   */
  @Input() latitude = 0;
  /**
   * Longitude for the coordinates.
   */
  @Input() longitude = 0;
  /**
   * Info window should be displayed after clicking on marker.
   */
  @Input() enableInfoWindow = false;
  /**
   * Object to describe the map marker icon.
   */
  @Input() icons: MarkerIcons | null = {};
  @Input()
  set locations(locations: Place[]) {
    this.places = locations;
    if (this.mapHelpers) {
      this.drawMarkers();
    }
  }
  @Input()
  set selectedPlaceId(id: string | undefined) {
    this.selectedId = id;
    if (this.mapHelpers) {
      this.drawMarkers();
    }
  }
  /**
   * EventEmitter for triggering a update event.
   */
  @Output() readonly update: EventEmitter<PlacesGetParams> =
    new EventEmitter<PlacesGetParams>();
  /**
   * EventEmitter for place selection update event.
   */
  @Output() readonly selected: EventEmitter<PlaceItem> =
    new EventEmitter<PlaceItem>();
  /**
   * EventEmitter for triggering a locate event.
   */
  @Output() readonly locate: EventEmitter<google.maps.LatLngLiteral> =
    new EventEmitter<google.maps.LatLngLiteral>();
  /**
   * EventEmitter for triggering a mapReady event.
   */
  @Output() readonly mapReady: EventEmitter<undefined> = new EventEmitter();

  @ViewChild('bbMapInfoWindowContent') template:
    | ElementRef<HTMLElement>
    | undefined;

  private currentLocationIconOptions: google.maps.Symbol | undefined;
  private mapHelpers: MapHelpersService | undefined;

  selectedId: string | undefined;
  places: Place[] | undefined;
  markers: Marker[] = [];
  infoWindow: google.maps.InfoWindow | undefined;
  private getInfoWindowContent(placeId: string) {
    const wrapperElement = (<ElementRef<HTMLElement>>this.template)
      .nativeElement;
    const template = wrapperElement.querySelector(
      `div[data-place-id='${placeId}']`
    );
    return template ? template.innerHTML : '';
  }

  private getDistance(
    bounds: google.maps.LatLngBounds | null | undefined
  ): number {
    if (!bounds) {
      return 0;
    }

    return (
      google.maps.geometry.spherical.computeDistanceBetween(
        bounds.getNorthEast(),
        bounds.getSouthWest()
      ) / 1000
    );
  }

  private emitMapUpdate() {
    if (!this.mapHelpers) {
      return;
    }

    const center = this.mapHelpers.getCenter();

    this.update.emit({
      latitude: center.lat(),
      longitude: center.lng(),
      radius: Math.ceil(this.getDistance(this.mapHelpers.getBounds()) / 2),
    });
  }

  private setCurrentLocationMarker(location: google.maps.LatLngLiteral) {
    if (!this.mapHelpers) {
      return;
    }

    this.mapHelpers.createMarker({
      position: location,
      clickable: false,
      icon: this.currentLocationIconOptions,
      locationId: '',
    });
  }

  private markerClicklistener(marker: Marker, place: PlaceItem) {
    const helpers = <MapHelpersService>this.mapHelpers;

    if (this.enableInfoWindow) {
      if (this.infoWindow) {
        this.infoWindow.close();
      }

      const content = this.getInfoWindowContent(place.id);
      this.infoWindow = helpers.createInfoWindow({ content });
      helpers.openInfoWindow(this.infoWindow, marker);
    }

    if (this.mapHelpers) {
      if (marker.locationId === this.selectedId) {
        this.selected.emit({
          id: '',
          latitude: 0,
          longitude: 0,
        });
      } else {
        this.selected.emit(
          (<Place[]>this.places).find(
            (location) => location.id === marker.locationId
          )
        );
      }
    }
  }

  private drawMarkers() {
    if (!Array.isArray(this.places)) {
      return;
    }

    // markers that are not needed anymore, should be cleared
    const placeIds = this.places.map((place) => place.id);
    const newMarkerList: Marker[] = [];
    this.markers.forEach((marker) => {
      if (placeIds.includes(marker.locationId)) {
        newMarkerList.push(marker);
      } else {
        // eslint-disable-next-line
        marker.setMap(null);
      }
    });
    this.markers = newMarkerList;

    // places markers on map
    this.places.forEach((place) => {
      let marker: Marker | undefined = this.markers.find(
        (item) => item.locationId === place.id
      );
      if (marker === undefined) {
        marker = this.createMarker(place);
        this.markers.push(marker);
        marker.addListener('click', () =>
          this.markerClicklistener(<Marker>marker, place)
        );
      }

      if (this.selectedId && placeIds.includes(this.selectedId)) {
        if (place.id === this.selectedId) {
          marker.setOpacity(1);
          marker.setIcon({
            url: (<google.maps.Icon>marker.getIcon()).url,
            scaledSize: new google.maps.Size(60, 60),
          });
        } else {
          marker.setOpacity(0.5);
          marker.setIcon({
            url: (<google.maps.Icon>marker.getIcon()).url,
          });
        }
      } else {
        marker.setOpacity(1);
        marker.setIcon({
          url: (<google.maps.Icon>marker.getIcon()).url,
        });
      }
    });
  }

  private createMarker(place: PlaceItem) {
    const helpers = <MapHelpersService>this.mapHelpers;

    const position = {
      lat: place.latitude,
      lng: place.longitude,
    };

    // try to find place type icon or try to use fallback icon
    const defaultIcon =
      this.icons && Object.keys(this.icons).length
        ? this.icons[Object.keys(this.icons)[0]]
        : undefined;
    const iconUrl =
      place.placeType && this.icons && this.icons[place.placeType]
        ? this.icons[place.placeType]
        : defaultIcon;

    let marker = {
      position,
      locationId: place.id,
      title: $localize`:@@places.map.marker.title:map marker`,
    };

    // if there is no icon at all, use maps default pin
    if (iconUrl) {
      marker = {
        ...marker,
        ...{
          icon: {
            url: iconUrl,
          },
        },
      };
    }

    return helpers.createMarker(marker);
  }

  /**
   * List of locations to be rendered.
   */
  get locations(): Place[] {
    return this.places || [];
  }

  /**
   * Selected place id.
   */
  get selectedPlaceId() {
    return this.selectedId;
  }

  get options(): google.maps.MapOptions {
    return {
      center: {
        lat: this.latitude,
        lng: this.longitude,
      },
      zoom: this.mapZoom,
      disableDefaultUI: true, // a way to quickly hide all controls
      zoomControl: true, // allow zoom only
      styles: [
        {
          featureType: 'poi',
          stylers: [{ visibility: 'off' }],
        },
      ],
    };
  }

  setMapOptions(component: MapComponent) {
    this.mapHelpers = component.mapHelpers;
    this.mapHelpers.setMapOptions({
      mapTypeControlOptions: {
        mapTypeIds: [google.maps.MapTypeId.ROADMAP],
      },
    });

    this.mapHelpers.onMapEvent('idle', () => this.emitMapUpdate());

    this.currentLocationIconOptions = {
      path: google.maps.SymbolPath.CIRCLE,
      fillColor: '#4285F4',
      fillOpacity: 1,
      scale: 6,
      strokeColor: 'white',
      strokeWeight: 2,
    };

    if ('geolocation' in navigator) {
      const helpers = this.mapHelpers;
      navigator.geolocation.getCurrentPosition((location) => {
        const position = {
          lat: location.coords.latitude,
          lng: location.coords.longitude,
        };
        helpers.setCenter(position);
        this.setCurrentLocationMarker(position);
        this.locate.emit(position);
      });
    }

    // Everything set, emit that map is ready
    this.mapReady.emit();
  }

  ngOnChanges(changes: SimpleChanges) {
    if (!this.mapHelpers) {
      return;
    }

    if (changes.latitude || changes.longitude) {
      this.mapHelpers.setCenter({
        lat: changes.latitude ? changes.latitude.currentValue : this.latitude,
        lng: changes.longitude
          ? changes.longitude.currentValue
          : this.longitude,
      });
    }
  }
}
