import { Component, Input, ChangeDetectionStrategy } from '@angular/core';
import { DatePipe } from '@angular/common';
import { PlaceAddress, Hour } from '@backbase/data-ang/places';

import { PlaceItem, WeekDay } from '../../model/place-model';

@Component({
  selector: 'bb-place-details',
  templateUrl: './place-details.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
  providers: [DatePipe],
})
export class PlaceDetailsComponent {
  /**
   * The place details to be shown.
   */
  @Input() place: PlaceItem | undefined;

  // This returns array of weekDay numbers based on the WeekDay enum
  get weekDay() {
    const days = Object.keys(WeekDay);
    return days.slice(0, days.length / 2).map((day) => +day);
  }

  getWorkTimes(day: number, hours: Hour[]): Hour[] {
    return hours.filter((time: Hour) => time.day === day);
  }

  getFullAddress(address: PlaceAddress | undefined): string {
    return address
      ? encodeURI(`${address.postalCode} ${address.addressLine1}`)
      : '';
  }
}
