import { Injectable } from '@angular/core';

export enum GoogleMapsScriptProtocol {
  HTTP = 1,
  HTTPS = 2,
  AUTO = 3,
}

/**
 * Configuration for the {@link MapAPILoaderService}.
 */
export interface MapsAPIConfig {
  /**
   * The Google Maps API Key (see:
   * https://developers.google.com/maps/documentation/javascript/get-api-key)
   */
  apiKey?: string;

  /**
   * The Google Maps client ID (for premium plans).
   * When you have a Google Maps APIs Premium Plan license, you must authenticate
   * your application with either an API key or a client ID.
   * The Google Maps API will fail to load if both a client ID and an API key are included.
   */
  clientId?: string;

  /**
   * The Google Maps channel name (for premium plans).
   * A channel parameter is an optional parameter that allows you to track usage under your client
   * ID by assigning a distinct channel to each of your applications.
   */
  channel?: string;

  /**
   * Google Maps API version.
   */
  apiVersion?: string;

  /**
   * Host and Path used for the `<script>` tag.
   */
  hostAndPath?: string;

  /**
   * Protocol used for the `<script>` tag.
   */
  protocol?: GoogleMapsScriptProtocol;

  /**
   * Defines which Google Maps libraries should get loaded.
   */
  libraries?: string[];

  /**
   * The default bias for the map behavior is US.
   * If you wish to alter your application to serve different map tiles or bias the
   * application, you can overwrite the default behavior (US) by defining a `region`.
   * See https://developers.google.com/maps/documentation/javascript/basics#Region
   */
  region?: string;

  /**
   * The Google Maps API uses the browser's preferred language when displaying
   * textual information. If you wish to overwrite this behavior and force the API
   * to use a given language, you can use this setting.
   * See https://developers.google.com/maps/documentation/javascript/basics#Language
   */
  language?: string;
}

@Injectable()
export class MapAPILoaderService {
  private readonly scriptID: string = 'bbGoogleMapsApiScript';
  private readonly callbackName: string = 'bbMapsAPILoader';
  private readonly apiLoadPromiseName: string = 'bbMapsAPILoaderPromise';
  private readonly windowRef: Window = window;
  private readonly documentRef: Document = document;
  private configValue: MapsAPIConfig = {};

  get config() {
    return this.configValue;
  }

  set config(config: MapsAPIConfig) {
    this.configValue = config;
  }

  get loadAPIPromise() {
    return (<any>this.windowRef)[this.apiLoadPromiseName];
  }

  private assignScriptLoadingPromise(scriptElem: HTMLElement) {
    (<any>this.windowRef)[this.apiLoadPromiseName] = new Promise<void>(
      (resolve: Function, reject: Function) => {
        (<any>this.windowRef)[this.callbackName] = () => {
          resolve();
        };

        scriptElem.onerror = (error: Event | string) => {
          reject(error);
        };
      }
    );
  }

  private getScriptSrc(callbackName: string): string {
    const protocolType: GoogleMapsScriptProtocol =
      (this.configValue && this.configValue.protocol) ||
      GoogleMapsScriptProtocol.HTTPS;
    let protocol = '';

    switch (protocolType) {
      case GoogleMapsScriptProtocol.HTTP:
        protocol = 'http:';
        break;
      case GoogleMapsScriptProtocol.HTTPS:
        protocol = 'https:';
        break;
    }

    const hostAndPath: string =
      this.configValue.hostAndPath || 'maps.googleapis.com/maps/api/js';
    // make sure we have places library for search component
    const libraries: string[] = this.configValue.libraries || [];
    if (libraries.indexOf('places') === -1) {
      this.configValue.libraries = [...libraries, 'places'];
    }

    const queryParams: { [key: string]: string | string[] | undefined } = {
      v: this.configValue.apiVersion || 'quarterly',
      callback: callbackName,
      key: this.configValue.apiKey,
      client: this.configValue.clientId,
      channel: this.configValue.channel,
      libraries: this.configValue.libraries,
      region: this.configValue.region,
      language: this.configValue.language,
    };
    const params: string = Object.keys(queryParams)
      .filter((key: string) => queryParams[key] !== undefined)
      .filter((key: string) => {
        // remove empty arrays
        return (
          !Array.isArray(queryParams[key]) ||
          (Array.isArray(queryParams[key]) &&
            (<string[]>queryParams[key]).length > 0)
        );
      })
      .map((key: string) => {
        let value = <string | string[]>queryParams[key];
        if (Array.isArray(value)) {
          // join arrays as comma seperated strings
          value = value.join(',');
        }
        return `${key}=${value}`;
      })
      .join('&');
    return `${protocol}//${hostAndPath}?${params}`;
  }

  load(): Promise<void> {
    const window = <any>this.windowRef;
    if (window.google && window.google.maps) {
      // Google maps already loaded on the page.
      return Promise.resolve();
    }

    if (this.loadAPIPromise) {
      return this.loadAPIPromise;
    }

    const script = this.documentRef.createElement('script');
    script.type = 'text/javascript';
    script.async = true;
    script.defer = true;
    script.id = this.scriptID;
    script.src = this.getScriptSrc(this.callbackName);

    this.assignScriptLoadingPromise(script);
    this.documentRef.body.appendChild(script);
    return this.loadAPIPromise;
  }
}
