import { Component, Input, ChangeDetectionStrategy } from '@angular/core';
import { DatePipe } from '@angular/common';
import { Hour } from '@backbase/data-ang/places';

@Component({
  selector: 'bb-work-times',
  templateUrl: './work-times.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
  providers: [DatePipe],
})
export class WorkTimesComponent {
  /**
   * The day number based on WeekDay enum
   */
  @Input() day = 0;
  /**
   * Hours related to this day
   */
  @Input() hours: Hour[] = [];

  constructor(private readonly datePipe: DatePipe) {}

  toLocaleTime(hour: string): string {
    return (
      this.datePipe.transform(new Date('2000-01-01T' + hour), 'shortTime') ||
      hour
    );
  }

  isToday(day: number, today: number = new Date().getDay()) {
    today = today || 7;
    return day === today;
  }
}
