import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';

import { PlacesHttpService } from '@backbase/data-ang/places';

import { Place } from '../model/place-model';
import { PlacesGetParams } from '../model/place-param-model';

@Injectable()
export class PlacesService {
  constructor(private readonly placesDataHttpService: PlacesHttpService) {}

  getPlaces(params: PlacesGetParams): Observable<Place[]> {
    return this.placesDataHttpService
      .getPlaces(params)
      .pipe(map((data) => data || []));
  }
}
