import {
  Component,
  ChangeDetectionStrategy,
  Input,
  Output,
  EventEmitter,
  ViewChild,
  OnInit,
} from '@angular/core';
import { MapAPILoaderService, MapsAPIConfig } from './map-api-loader.service';
import { SearchBoxComponent } from '@backbase/ui-ang/search-box';

@Component({
  selector: 'bb-map-search-ui',
  templateUrl: './map-search.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class MapSearchComponent implements OnInit {
  /**
   * Map autocomplete options object.
   */
  @Input() options: google.maps.places.AutocompleteOptions = {};
  /**
   * Map API configuration.
   */
  @Input() config: MapsAPIConfig = {};

  /**
   * EventEmitter for triggering a mapSearch event.
   */
  @Output()
  readonly mapSearch: EventEmitter<google.maps.places.PlaceResult> = new EventEmitter<google.maps.places.PlaceResult>();

  @ViewChild('bbMapsearch', { static: true }) searchBox:
    | SearchBoxComponent<string>
    | undefined;

  constructor(private readonly loader: MapAPILoaderService) {}

  onSearchButtonClick() {
    const input = (<SearchBoxComponent<string>>this.searchBox).inputField;
    if (!input) {
      return;
    }

    // select first result and do the search
    google.maps.event.trigger(input.nativeElement, 'focus', {});
    google.maps.event.trigger(input.nativeElement, 'keydown', {
      keyCode: 40, // arrow down
      stopPropagation: () => undefined, // because these get called
      preventDefault: () => undefined,
    });
    google.maps.event.trigger(input.nativeElement, 'keydown', { keyCode: 13 }); // enter
  }

  ngOnInit() {
    this.loader.config = this.config;
    this.loader.load().then(() => {
      const input = (<SearchBoxComponent<string>>this.searchBox).inputField;
      if (!input) {
        return;
      }

      const autocomplete = new google.maps.places.Autocomplete(
        input.nativeElement,
        this.options
      );
      autocomplete.addListener('place_changed', () => {
        const place = autocomplete.getPlace();

        if (place.geometry) {
          this.mapSearch.emit(place);
        }
      });
    });
  }
}
