import { Injectable } from '@angular/core';
import { MapAPILoaderService, MapsAPIConfig } from './map-api-loader.service';
import { MarkerOptions, Marker } from './map.component';

/**
 * Handles the communication with the Google Maps Javascript
 * API v3
 */
@Injectable()
export class MapHelpersService {
  private map: google.maps.Map | undefined;

  constructor(private readonly loader: MapAPILoaderService) {}

  createMap(
    el: HTMLElement,
    mapOptions: google.maps.MapOptions,
    mapConfig: MapsAPIConfig = {}
  ): Promise<void> {
    this.loader.config = mapConfig;
    return this.loader.load().then(() => {
      this.map = new google.maps.Map(el, mapOptions);
    });
  }

  setMapOptions(options: google.maps.MapOptions) {
    (<google.maps.Map>this.map).setOptions(options);
  }

  /**
   * Creates a google map marker with the map context
   */
  createMarker(
    options: MarkerOptions = <MarkerOptions>{},
    addToMap: boolean = true
  ): Marker {
    if (addToMap) {
      options.map = this.map;
    }
    return new google.maps.Marker(options) as Marker;
  }

  createInfoWindow(
    options?: google.maps.InfoWindowOptions
  ): google.maps.InfoWindow {
    return new google.maps.InfoWindow(options);
  }

  openInfoWindow(
    infoWindow: google.maps.InfoWindow,
    marker: google.maps.Marker
  ) {
    return infoWindow.open(this.map, marker);
  }

  onMapEvent(eventName: string, callback: Function) {
    (<google.maps.Map>this.map).addListener(eventName, (arg) => callback(arg));
  }

  setCenter(latLng: google.maps.LatLngLiteral) {
    return (<google.maps.Map>this.map).setCenter(latLng);
  }

  getZoom(): number {
    return (<google.maps.Map>this.map).getZoom();
  }

  getBounds(): google.maps.LatLngBounds | null | undefined {
    return (<google.maps.Map>this.map).getBounds();
  }

  getMapTypeId(): google.maps.MapTypeId | string {
    return (<google.maps.Map>this.map).getMapTypeId();
  }

  setZoom(zoom: number) {
    return (<google.maps.Map>this.map).setZoom(zoom);
  }

  getCenter(): google.maps.LatLng {
    return (<google.maps.Map>this.map).getCenter();
  }

  panTo(latLng: google.maps.LatLng | google.maps.LatLngLiteral) {
    return (<google.maps.Map>this.map).panTo(latLng);
  }

  panBy(x: number, y: number) {
    return (<google.maps.Map>this.map).panBy(x, y);
  }

  fitBounds(
    latLng: google.maps.LatLngBounds | google.maps.LatLngBoundsLiteral
  ) {
    return (<google.maps.Map>this.map).fitBounds(latLng);
  }

  panToBounds(
    latLng: google.maps.LatLngBounds | google.maps.LatLngBoundsLiteral
  ) {
    return (<google.maps.Map>this.map).panToBounds(latLng);
  }

  /**
   * Triggers the given event name on the map instance.
   */
  triggerMapEvent(eventName: string) {
    return google.maps.event.trigger(this.map, eventName);
  }
}
