import {
  Component,
  Input,
  Output,
  EventEmitter,
  ViewChild,
  ElementRef,
  OnInit,
  ChangeDetectionStrategy,
} from '@angular/core';

import { MapHelpersService } from './map-helpers.service';
import { MapsAPIConfig } from './map-api-loader.service';

export interface MapDetails {
  center: google.maps.LatLng;
  bounds: google.maps.LatLngBounds | null | undefined;
  zoom: number;
}

export interface MarkerOptions extends google.maps.MarkerOptions {
  locationId: string;
}

export interface Marker extends google.maps.Marker {
  locationId: string;
}

@Component({
  selector: 'bb-map-ui',
  templateUrl: './map.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class MapComponent implements OnInit {
  /**
   * Map options object.
   */
  @Input() options: google.maps.MapOptions = {};
  /**
   * Map API configuration.
   */
  @Input() config: MapsAPIConfig = {};
  /**
   * EventEmitter for triggering a mapReady event.
   */
  @Output() readonly mapReady: EventEmitter<MapComponent> =
    new EventEmitter<MapComponent>();

  @ViewChild('bbMapContainer', { static: true }) mapContainer:
    | ElementRef<HTMLElement>
    | undefined;

  constructor(private readonly helpers: MapHelpersService) {}

  get mapHelpers(): MapHelpersService {
    return this.helpers;
  }

  ngOnInit() {
    this.helpers
      .createMap(
        (<ElementRef<HTMLElement>>this.mapContainer).nativeElement,
        this.options,
        this.config
      )
      .then(() => this.mapReady.emit(this));
  }
}
