import { Injectable, Optional, Inject, InjectionToken } from '@angular/core';
import { ItemModel } from '@backbase/foundation-ang/core';
import { PartialObserver } from 'rxjs';

export const PlacesJourneyConfigurationToken =
  new InjectionToken<PlacesJourneyConfiguration>(
    'PlacesJourneyConfiguration injection token'
  );

export enum PlacesMeasurementSystem {
  METRIC = 'metric',
  US_IMPERIAL = 'imperial',
}

export interface PlaceTypes {
  [placeType: string]: {
    iconName?: string;
    markerUrl?: string;
  };
}

export interface PlacesJourneyConfiguration {
  apiKey: string;
  placeTypes: PlaceTypes;
  mapZoom: number;
  latitude: number;
  longitude: number;
  radius: number;
  limitList: number;
  measurementSystem: PlacesMeasurementSystem;
  deElevatedHeader: boolean;
}

const configDefaults: PlacesJourneyConfiguration = {
  apiKey: '',
  placeTypes: {},
  mapZoom: 15,
  latitude: 52.3760992,
  longitude: 4.900779,
  radius: 2,
  limitList: 100,
  measurementSystem: PlacesMeasurementSystem.METRIC,
  deElevatedHeader: false,
};

@Injectable()
export class PlacesJourneyConfigService {
  private _config: PlacesJourneyConfiguration;

  constructor(
    @Optional()
    @Inject(PlacesJourneyConfigurationToken)
    config: PlacesJourneyConfiguration,
    @Optional() private readonly itemModel: ItemModel
  ) {
    config = config || {};
    this._config = { ...configDefaults, ...config };
    // update the API key from ItemModel if it was not provided from a static config
    if (this.itemModel && config.apiKey === undefined) {
      this.itemModel
        .property<string>('apiKey', this._config.apiKey)
        .subscribe(<PartialObserver<string>>{
          next: (key: string) => {
            this._config.apiKey = key;
          },
        })
        .unsubscribe();
    }
  }

  get defaults(): PlacesJourneyConfiguration {
    return configDefaults;
  }

  /**
   * API key for Google Maps
   */
  get apiKey(): string {
    return this._config.apiKey;
  }

  /**
   * Place types configuration. Used to set up marker image and the filter label for any place type available in the service
   */
  get placeTypes(): PlaceTypes {
    return this._config.placeTypes;
  }

  /**
   * Default zoom value for a map
   */
  get mapZoom(): number {
    return this._config.mapZoom;
  }

  /**
   * Default map latitude
   */
  get latitude(): number {
    return this._config.latitude;
  }

  /**
   * Default map longitude
   */
  get longitude(): number {
    return this._config.longitude;
  }

  /**
   * Default radius in which the locations are being searched while map is not loaded
   */
  get radius(): number {
    return this._config.radius;
  }

  /**
   * Maximum amount of locations to display
   */
  get limitList(): number {
    return this._config.limitList;
  }

  /**
   * Measurement system
   */
  get measurementSystem(): PlacesMeasurementSystem {
    return this._config.measurementSystem;
  }

  /**
   * Dev config to enable de-elevated header
   */
  get deElevatedHeader(): boolean {
    return this._config.deElevatedHeader;
  }
}
