import {
  Component,
  OnInit,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  ElementRef,
  ViewChild,
} from '@angular/core';

import { HttpErrorResponse } from '@angular/common/http';
import { Observable, combineLatest, of, Subject } from 'rxjs';
import { filter, map, switchMap, catchError } from 'rxjs/operators';

import { AssetsResolver, AssetsService } from '@backbase/foundation-ang/core';

import {
  defaultPlaceTypeMarkerURI,
  defaultPlaceType,
} from '../model/constants';
import { Place, MarkerIcons, PlaceItem } from '../model/place-model';
import { PlacesGetParams } from '../model/place-param-model';
import { parseError, PlacesError } from '../model/place-error-model';
import { PlacesService } from '../services/places.service';
import {
  MapsAPIConfig,
  MapAPILoaderService,
} from '../components/bb-map-ui/map-api-loader.service';

import {
  PlacesJourneyConfigService,
  PlacesMeasurementSystem,
} from '../services/places-journey-config.service';

@Component({
  selector: 'bb-places-view',
  templateUrl: './places-view.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
  providers: [MapAPILoaderService],
})
export class PlacesViewComponent implements OnInit {
  // This is required to show info window only on mobile screen
  @ViewChild('groupBtns')
  set groupBtns(elRef: ElementRef | undefined) {
    this.groupBtnRef = elRef;
    setTimeout(() => {
      if (!this.groupBtnRef) {
        return;
      }
      this.smallScreen =
        window.getComputedStyle(this.groupBtnRef.nativeElement).display !==
        'none';
    }, 0);
  }

  private groupBtnRef: ElementRef | undefined;
  private readonly mapParamsSubject = new Subject<PlacesGetParams>();
  private readonly mapParams = this.mapParamsSubject.asObservable();
  readonly selectedPlaceId = new Subject<string>();
  private readonly resolveAsset: AssetsResolver =
    this.assetsService.createAssetsResolver({
      widgetName: 'bb-places-journey-ang',
      getURIFromAssetID: (placeType: string) => {
        const found = this.placeTypes.find((type) => type === placeType);
        return found
          ? this.config.placeTypes[found].markerUrl || defaultPlaceTypeMarkerURI
          : defaultPlaceTypeMarkerURI;
      },
    });
  private mapConfigObject: MapsAPIConfig = {};
  private geocoder: google.maps.Geocoder | undefined;

  readonly locations: Observable<Place[]> = this.mapParams.pipe(
    filter((params) => params !== undefined),
    switchMap((params) => this.service.getPlaces(params)),
    map((data) =>
      this.config.limitList ? data.slice(0, this.config.limitList) : data
    ),
    catchError((error: HttpErrorResponse) => {
      this.handleError(error);
      return of([]);
    })
  );

  error: PlacesError | undefined;
  selectedView: 'list' | 'map' = 'list';
  smallScreen: boolean | undefined;
  currentLocation: string | undefined;
  currentPosition: google.maps.LatLngLiteral | undefined;
  latitude = 0;
  longitude = 0;
  radius = 1;
  limitList: number | undefined;
  types: string[] = [];

  constructor(
    readonly config: PlacesJourneyConfigService,
    private readonly service: PlacesService,
    private readonly assetsService: AssetsService,
    private readonly cd: ChangeDetectorRef,
    private readonly loader: MapAPILoaderService
  ) {
    this.latitude = this.config.latitude;
    this.longitude = this.config.longitude;
    this.radius = this.config.radius;
    this.limitList = this.config.limitList;
  }

  private loadApi(mapConfig: MapsAPIConfig = {}): Promise<void> {
    this.loader.config = mapConfig;
    return this.loader.load().then(() => {
      this.geocoder = new google.maps.Geocoder();
      return;
    });
  }

  private getCurrentLocation() {
    if ('geolocation' in navigator) {
      navigator.geolocation.getCurrentPosition((location) => {
        this.currentPosition = {
          lat: location.coords.latitude,
          lng: location.coords.longitude,
        };
        if (!this.geocoder) {
          return;
        }
        this.geocoder.geocode(
          { location: this.currentPosition },
          (
            results: google.maps.GeocoderResult[],
            status: google.maps.GeocoderStatus
          ) => {
            if (status === google.maps.GeocoderStatus.OK && results[0]) {
              const route = results[0].address_components.filter((item) =>
                item.types.find((type) => type === 'route')
              );
              this.currentLocation =
                route[0].long_name || results[0].formatted_address;
            }
          }
        );
      });
    }
  }

  private handleError(error: HttpErrorResponse) {
    this.error = parseError(error);
  }

  get groupBtns(): ElementRef<any> | undefined {
    return this.groupBtnRef;
  }

  get mapConfig(): MapsAPIConfig {
    return this.mapConfigObject;
  }

  get placeTypes(): string[] {
    return Object.keys(this.config.placeTypes);
  }

  get icons(): Observable<MarkerIcons> {
    const types: string[] = [defaultPlaceType].concat(this.placeTypes);
    return combineLatest(types.map((type) => this.resolveAsset(type))).pipe(
      map((assets) => {
        const icons: MarkerIcons = {};
        for (let i = 0; i < assets.length; i++) {
          icons[types[i]] = assets[i];
        }
        return icons;
      })
    );
  }

  get isMetric(): boolean {
    return this.config.measurementSystem !== PlacesMeasurementSystem.METRIC;
  }

  filterTypes(values: { [type: string]: boolean }): string[] {
    const types: string[] = [];
    Object.keys(values).forEach((key: string) => {
      if (values[key]) {
        types.push(key);
      }
    });

    return types;
  }

  locationUpdate(params: PlacesGetParams) {
    this.latitude = params.latitude;
    this.longitude = params.longitude;
    this.radius = params.radius || this.config.radius;
    this.types = params.types || this.types;
    this.cd.detectChanges();
    this.mapParamsSubject.next({
      ...params,
      radius: this.config.radius,
      types: this.types,
    });
  }

  handleTypeSearch(types: string[]) {
    this.locationUpdate({
      latitude: this.latitude,
      longitude: this.longitude,
      radius: this.radius,
      types,
    });
  }

  handleMapSearch(place: google.maps.places.PlaceResult) {
    if (!place.geometry) {
      return;
    }

    this.locationUpdate({
      latitude: place.geometry.location.lat(),
      longitude: place.geometry.location.lng(),
      radius: this.config.radius,
      types: this.types,
    });
  }

  updatePlaceSelection(place: PlaceItem) {
    this.selectedPlaceId.next(place.id);
  }

  onMapReady() {
    // Map is there, so we don't use list limit property to determine list size. Map's zoom and radius parameter are driving it from now on
    this.limitList = undefined;

    this.locationUpdate({
      latitude: this.latitude,
      longitude: this.longitude,
      radius: this.radius,
      types: this.types,
    });
  }

  ngOnInit() {
    this.mapConfigObject = {
      apiKey: this.config.apiKey,
      libraries: ['geometry'],
    };

    this.loadApi(this.mapConfigObject).then(() => {
      this.getCurrentLocation();
    });

    this.locationUpdate({
      latitude: this.config.latitude,
      longitude: this.config.longitude,
      radius: this.config.radius,
    });
  }
}
